const catalogList = document.querySelectorAll(".catalog__list");
const category__btn = document.querySelectorAll(".category__btn");

document.querySelector('.category').addEventListener('click', (event) => {
  // event.preventDefault();


  category__btn.forEach(el => {
    el.classList.remove('category__btn--active');
  });

  if (!event.target.classList.contains('category__btn--active')) {
    event.target.classList.add('category__btn--active');
  } else {
    event.target.classList.remove('category__btn--active');
  }


  let filterClass = event.target.dataset['f'];

  catalogList.forEach( elem => {
    if (!elem.classList.contains(filterClass)) {
      elem.classList.add('hide');
      elem.classList.remove('open');
    } else {
      elem.classList.add('open');
    }
  });
});
