function plus(array) {
  array.value = parseInt(array.value) + 1;
}

function minus(array) {
  if (array.value > 1) {
    array.value = parseInt(array.value) - 1;
  }
}

let quantity = document.querySelectorAll('.quantity');
let quantityMinus = document.querySelectorAll('.quantity--minus');
let quantityPlus = document.querySelectorAll('.quantity--plus');


// quantityPlus.forEach( elem => {
//
//   console.log(elem.tagName);
//
// });

quantityPlus.forEach((element)  => {
  element.addEventListener('click', (event) => {
    console.log(event.target);
    let amount = event.target.previousSibling.previousElementSibling;
    amount.value = parseInt(amount.value) +1;
  })
});

quantityMinus.forEach((element)  => {
  element.addEventListener('click', (event) => {
    console.log(event.target);
    let amount = event.target.nextSibling.nextElementSibling;
    if (amount.value > 1) {
      amount.value = parseInt(amount.value) -1;
    }
  })
});
