import './_vendor';
import vars from './_vars';
import './_functions';
import './_components';
import { disableScroll } from './functions/disable-scroll';
import { enableScroll } from './functions/enable-scroll';

document.querySelector('.calculate__submit').addEventListener('click', function (event) {
  event.preventDefault();
  document.querySelector('.overlay').classList.add('overlay--visible');
  document.querySelector('.popup__wrapper').classList.add('popup__wrapper--visible');
  disableScroll();
});

document.querySelector('.close_button').addEventListener('click', function (event) {
  document.querySelector('.overlay').classList.remove('overlay--visible');
  document.querySelector('.popup__wrapper').classList.remove('popup__wrapper--visible');
  enableScroll();
});
