document.querySelector('.calculate__block').addEventListener('click', function (event) {
  let currentTarget = event.target;

  if (currentTarget.classList.contains('plus')) {
    event.preventDefault();
    let count = currentTarget.parentElement.querySelector('input');
    let value = count.value;
    value = parseInt(value) +1;
    count.value = value;
  } else if (currentTarget.classList.contains('minus')) {
    event.preventDefault();
    let count = currentTarget.parentElement.querySelector('input');
    let value = count.value;
    if (value > 1) {
      value = parseInt(value) -1;
      count.value = value;
    }
  }
});
